.container {
  height: 100vh;
  display: flex;
  justify-content: center;
  background: linear-gradient(45deg, Violet, Orange);
}
.movies {
  display: grid;
  grid-template-columns: repeat(2, minmax(400px, 1fr));
  grid-gap: 100px;
  width: 50%;
  padding: 50px;
  padding-top: 50px;
  
}
